@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh; }

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  font-family: "Roboto", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

a {
  color: #000000;
  text-decoration: none; }

img,
svg,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.rouble {
  font-family: "Rouble", "arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page__body {
  padding: 40px 0;
  position: relative;
  overflow-x: hidden; }
  @media (max-width: 767px) {
    .page__body {
      padding: 10px 0; } }

.page__background {
  position: fixed;
  top: 0;
  object-fit: cover;
  object-position: 55%;
  width: 100%;
  z-index: -1;
  height: 100%;
  min-height: 100vh; }

.starships {
  max-width: 1180px;
  margin: 0 auto 150px;
  padding: 40px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 40px;
  text-shadow: 2px 2px 8px #000000; }
  @media (max-width: 1279px) {
    .starships {
      margin: 0 40px 140px; } }
  @media (max-width: 767px) {
    .starships {
      border-radius: 20px;
      margin: 0 10px 120px;
      padding: 20px 10px; } }

.starships__heading {
  font-size: 46px;
  line-height: 125%; }
  @media (max-width: 1279px) {
    .starships__heading {
      font-size: 36px; } }
  @media (max-width: 767px) {
    .starships__heading {
      font-size: 26px; } }

@media (max-width: 767px) {
  .starships__buttons {
    display: flex;
    flex-direction: column;
    align-items: center; } }

.starships__button {
  color: inherit;
  border: none;
  border-radius: 5px;
  padding: 10px 40px;
  font-weight: 500;
  text-transform: uppercase;
  transition: 0.3s ease;
  margin-right: 20px;
  margin-bottom: 10px; }
  @media (hover: hover) {
    .starships__button:hover:not(.focus-visible) {
      box-shadow: 0 0 20px #ffffff;
      transform: scale(1.2);
      color: #000000; } }
  .starships__button.focus-visible:focus {
    box-shadow: 0 0 20px #ffffff;
    transform: scale(1.2);
    color: #000000; }
  .starships__button--get {
    background-color: rgba(102, 255, 0, 0.4); }
    @media (hover: hover) {
      .starships__button--get:hover:not(.focus-visible) {
        background-color: rgba(102, 255, 0, 0.8); } }
    .starships__button--get.focus-visible:focus {
      background-color: rgba(102, 255, 0, 0.8); }
  .starships__button--delete {
    background-color: rgba(230, 66, 107, 0.6); }
    @media (hover: hover) {
      .starships__button--delete:hover:not(.focus-visible) {
        background-color: #e6426b; } }
    .starships__button--delete.focus-visible:focus {
      background-color: #e6426b; }

.starships__table-wrapper {
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-top: 50px;
  max-height: 250px; }
  .starships__table-wrapper::-webkit-scrollbar {
    width: 0;
    height: 0; }
  @media (max-width: 767px) {
    .starships__table-wrapper {
      max-height: 300px;
      margin-top: 20px; } }

.table {
  border-collapse: collapse;
  min-width: 100%;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.6);
  color: #000000;
  text-shadow: 2px 2px 8px #ffffff; }
  @media (max-width: 767px) {
    .table {
      font-size: 12px; } }

.table thead {
  position: sticky;
  top: 0;
  z-index: 1; }

.table td,
.table th {
  border: 1px solid #000000;
  padding: 2px 10px; }
  .table td:first-child,
  .table th:first-child {
    border-left: none; }
  .table td:last-of-type,
  .table th:last-of-type {
    border-right: none;
    padding-right: 25px; }

.table th {
  cursor: pointer;
  border-top: none;
  text-shadow: 0 0 4px #81e99f;
  padding: 10px 10px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 20%, rgba(255, 255, 255, 0.1) 25%, rgba(255, 255, 255, 0.4) 30%, #ffffff 50%, #ffffff 100%); }

.table tr {
  position: relative; }

.table tr:last-child td {
  border-bottom: none; }

.table__cap--hidden {
  display: none; }

.table__cap td {
  text-align: center;
  padding: 10px;
  border-radius: 0 0 15px 15px;
  background-color: rgba(235, 55, 65, 0.7);
  font-weight: 500;
  text-transform: uppercase; }

.table__data-row td {
  position: relative;
  transition: all 1s, color 0.5s ease-in 0.5s; }
  .table__data-row td::after {
    transition: 0.3s ease, opacity 0.5s ease-in 0.5s;
    position: absolute;
    top: 50%;
    left: 0;
    content: "";
    width: 0;
    height: 1px;
    background-color: #201515; }

.table__data-row--collapsing td {
  color: transparent; }
  .table__data-row--collapsing td::after {
    width: 100%;
    opacity: 0; }

.table__delete-row-button {
  position: absolute;
  cursor: pointer;
  padding: 1px;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
  background: url("../img/svg/cross.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 11px;
  height: 11px;
  border: none;
  transition: 0.3s ease; }
  @media (hover: hover) {
    .table__delete-row-button:hover:not(.focus-visible) {
      opacity: 0.2; } }
  .table__delete-row-button.focus-visible:focus {
    opacity: 0.2; }

.table__sorting {
  position: relative; }
  .table__sorting::after {
    position: absolute;
    top: 48%;
    right: 5px;
    content: "";
    border: 8px solid transparent;
    border-bottom: 8px solid #000000;
    vertical-align: middle;
    transform: translateY(-50%); }

.table__sorting-reverse {
  position: relative; }
  .table__sorting-reverse::after {
    position: absolute;
    top: 48%;
    right: 5px;
    content: "";
    border: 8px solid transparent;
    border-bottom: 8px solid #000000;
    vertical-align: middle;
    transform: rotate(180deg); }

.preloader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8vmax;
  height: 8vmax;
  border-right: 4px solid #ffffff;
  border-radius: 100%;
  animation: spinRight 800ms linear infinite; }
  .preloader::before, .preloader::after {
    content: "";
    width: 6vmax;
    height: 6vmax;
    display: block;
    position: absolute;
    top: calc(50% - 3vmax);
    left: calc(50% - 3vmax);
    border-left: 3px solid #ffffff;
    border-radius: 100%;
    animation: spinLeft 800ms linear infinite; }
  .preloader::after {
    width: 4vmax;
    height: 4vmax;
    top: calc(50% - 2vmax);
    left: calc(50% - 2vmax);
    border: 0;
    border-right: 2px solid #ffffff;
    animation: none; }
  .preloader--hiding {
    transition: 0.3s opacity;
    opacity: 0; }
  .preloader--hidden {
    visibility: hidden; }

@keyframes spinLeft {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(720deg); } }

@keyframes spinRight {
  from {
    transform: rotate(360deg); }
  to {
    transform: rotate(0deg); } }

.yoda {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  bottom: 0;
  right: 15%; }

.yoda__button {
  background: none;
  border: none;
  transition: all 0.6s;
  z-index: 10; }
  @media (max-width: 767px) {
    .yoda__button {
      right: 5%; } }
  @media (hover: hover) {
    .yoda__button:hover:not(.focus-visible) .yoda__pic {
      transform: scale(1.4); }
    .yoda__button:hover:not(.focus-visible) + .yoda__baloon {
      opacity: 1;
      transition: all 0.5s ease-in-out 0.1s;
      visibility: visible; } }
  .yoda__button.focus-visible:focus .yoda__pic {
    transform: scale(1.4); }
  .yoda__button.focus-visible:focus + .yoda__baloon {
    opacity: 1;
    transition: all 0.5s ease-in-out 0.1s;
    visibility: visible; }

.yoda__pic {
  transition: all 0.6s;
  transform-origin: 70% 100%; }
  @media (max-width: 767px) {
    .yoda__pic {
      height: 150px; } }

.yoda__baloon {
  position: absolute;
  padding: 0 10px;
  right: -20px;
  bottom: 290px;
  width: 440px;
  font-size: 36px;
  line-height: 120%;
  background-color: #ffffff;
  border: 10px solid #000000;
  border-radius: 45px;
  color: #000000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in 0s;
  z-index: 2; }
  @media (max-width: 767px) {
    .yoda__baloon {
      padding: 0 5px;
      bottom: 200px;
      right: 0;
      width: 280px;
      font-size: 20px;
      border-width: 5px;
      border-radius: 20px; } }

.yoda__text {
  text-align: center; }

.yoda__tail {
  position: absolute;
  right: 100px;
  bottom: -27px;
  width: 40px;
  height: 40px;
  border-top: 10px solid #000000;
  border-right: 10px solid #000000;
  background-color: #ffffff;
  transform: rotate(135deg); }
  @media (max-width: 767px) {
    .yoda__tail {
      bottom: -14px;
      right: 60px;
      width: 20px;
      height: 20px;
      border-width: 5px; } }
